* {
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

ul {
  padding: 0;
  margin: 0;
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
}

body {
  // Чтобы был прилипающий футер
  display: grid;
  grid-template-rows: auto 1fr auto;
  min-height: 100vh;
  padding: 0;
  margin: 0;
  font-family: $roboto;
  font-weight: $roboto-medium;
  font-size: $main-size-text;
  line-height: $main-interlining;
}

.hidden {
  display: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.freehosting-2domains-link {
  display: none;
}