* {
  box-sizing: border-box;
}

a {
  text-decoration: none;
}

ul {
  padding: 0;
  margin: 0;
  list-style: none;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

img {
  max-width: 100%;
  height: auto;
}

body {
  display: grid;
  grid-template-rows: auto 1fr auto;
  min-height: 100vh;
  padding: 0;
  margin: 0;
  font-family: "Roboto", "Verdana", sans-serif;
  font-weight: 500;
  font-size: 14px;
  line-height: 21px;
}

.hidden {
  display: none;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.freehosting-2domains-link {
  display: none;
}

@font-face {
  font-family: "Roboto";
  src: local("Roboto"), url("../fonts/Roboto-Regular.woff2") format("woff2"), url("../fonts/Roboto-Regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Roboto";
  src: local("Roboto"), url("../fonts/Roboto-Medium.woff2") format("woff2"), url("../fonts/Roboto-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "Roboto";
  src: local("Roboto"), url("../fonts/Roboto-Bold.woff2") format("woff2"), url("../fonts/Roboto-Bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}

.button {
  display: flex;
  justify-content: space-between;
  align-items: center;
  color: #ffffff;
  font-size: 12px;
  line-height: 21px;
  border-radius: 3px;
  padding-left: 14px;
  padding-right: 20px;
  padding-top: 7px;
  padding-bottom: 7px;
  text-align: right;
  border: none;
}

.button--available {
  background-color: #55c97b;
  transition: background-color, color, 0.2s ease-out;
}

.button--available:hover,
.button--available:focus {
  background-color: #058b31;
  color: #ffffff;
  transition: background-color, color, 0.2s ease-out;
}

.button--available:active {
  opacity: 0.5;
}

.button--disable {
  background-color: #b1b1b1;
  transition: background-color 0.2s ease-out;
}

.button--disable:hover,
.button--disable:focus {
  background-color: #545353;
  transition: background-color 0.2s ease-out;
}

.button--disable:active {
  opacity: 0.5;
}

.button--create-shop {
  width: 185px;
  text-align: right;
  margin-bottom: 35px;
  margin-top: 10px;
}

.button--management-shops {
  width: 149px;
}

.button--management-shops:nth-child(odd) {
  margin-right: 10px;
}

.button--icon-close {
  stroke: #ffffff;
}

.button--icon-pencil {
  stroke: #ffffff;
  fill: #ffffff;
}

.button--form {
  display: block;
  width: 151px;
  font-size: 14px;
  text-align: center;
}

.button--form:first-child {
  margin-right: 10px;
}

.account {
  background-color: #f8faff;
  min-height: 100vh;
}

.account__content-title {
  font-size: 20px;
  color: #343434;
  margin-bottom: 25px;
}

.account__content {
  padding: 22px 16px 20px 59px;
}

.account__content-breadcrumb {
  padding: 0;
  transform: translateX(11px);
}

.account__content-breadcrumb .breadcrumb-link {
  color: #656565;
}

.account__nav {
  max-width: 256px;
  padding-left: 25px;
  padding-top: 22px;
  background-color: #ffffff;
  min-height: 100vh;
}

.account__nav-item {
  position: relative;
  margin-bottom: 29px;
  cursor: pointer;
}

.account__nav-item:hover .account__nav-link,
.account__nav-item:focus .account__nav-link {
  color: #343434;
  transform: scale(1.1);
  transition: color, transform 0.2s ease-out;
}

.account__nav-item:hover .account__nav-item-svg,
.account__nav-item:focus .account__nav-item-svg {
  stroke: #6666CC;
}

.account__nav-item:active .account__nav-link {
  opacity: 0.5;
}

.account__nav-item:active .account__nav-item-svg {
  opacity: 0.5;
}

.account__nav-link {
  display: inline-block;
  color: #7d7d7d;
  transition: color, transform, font-size 0.2s ease-out;
}

.account__nav-item-svg {
  margin-right: 20px;
}

.account__nav-item-svg--home {
  stroke: #7f7b7b;
}

.account__nav-item-svg--shops {
  fill: #7f7b7b;
  stroke: #7f7b7b;
}

.account__nav-item-svg--megafon {
  fill: #7f7b7b;
}

.account__nav-item-svg--statistics {
  stroke: #7f7b7b;
}

.account__nav-item-svg--balance {
  stroke: #7f7b7b;
}

.account__nav-item-svg--avatar {
  stroke: #7f7b7b;
}

.account__nav-item-svg--faq {
  fill: #7f7b7b;
  stroke: #7f7b7b;
}

.account__nav-item-svg--support {
  fill: #7f7b7b;
}

.account__nav-item-svg--exit {
  stroke: #7f7b7b;
}

.account__logo {
  display: block;
  margin-bottom: 43px;
}

.account__nav-item--current .account__nav-link {
  color: #343434;
}

.account__nav-item--current .account__nav-item-svg {
  stroke: #6666CC;
}

.account__nav-item--current::before {
  position: absolute;
  content: "";
  top: -13px;
  left: -25px;
  width: 4px;
  height: 49px;
  background-color: #6666CC;
  box-shadow: 0 2px 10px rgba(102, 102, 204, 0.67);
  transition: left 0.2s ease-out;
}

.sign-up {
  padding: 0;
  width: 337px;
}

.sign-up__item {
  padding: 0;
}

.sign-up__item--avatar {
  display: flex;
  align-items: center;
  background-color: #ffffff;
  border-radius: 5px;
  padding: 10px 17px;
  box-shadow: 0px 0px 16px rgba(136, 167, 245, 0.18);
}

.sign-up__avatar {
  color: #7d7d7d;
}

.sign-up__avatar > img {
  width: 45px;
  height: 45px;
  border-radius: 50%;
  border: 2px solid #d9d9d9;
  margin-right: 17px;
}

.sign-up__item--mail {
  position: relative;
  width: 65px;
  height: 65px;
  border: 1px solid #d4d4d4;
  border-radius: 5px;
  margin-right: 16px;
}

.sign-up__mail {
  display: flex;
  justify-content: center;
  align-items: center;
  width: inherit;
  height: inherit;
}

.sign-up .sign-up__item--unread-email::before {
  position: absolute;
  content: "";
  top: 16px;
  left: 37px;
  width: 7px;
  height: 7px;
  background-color: #e42626;
  border-radius: 50%;
  border: 4px solid #ffffff;
  box-sizing: content-box;
}

.container-fluid {
  min-height: 100vh;
}

.breadcrumb-item {
  margin-right: 8px;
  font-size: 12px;
}

.breadcrumb-item.active {
  position: relative;
}

.breadcrumb-item + .breadcrumb-item::before {
  content: "";
  display: inline-block;
  position: absolute;
  float: none;
  padding-right: var(--bs-breadcrumb-item-padding-x);
  color: var(--bs-breadcrumb-divider-color);
  background-image: url("../img/content/icons/icon-breadcrumb.svg");
  background-repeat: no-repeat;
  width: 8px;
  height: 9px;
  top: 5px;
  left: -3px;
}

.table > :not(caption) > * > * {
  border-bottom-width: 0;
}

.table-shops {
  background-color: #ffffff;
  border-radius: 5px;
  padding: 16px 23px;
  box-shadow: 0 11px 21px rgba(40, 40, 40, 0.07);
}

.table-shops__table {
  margin: 0;
}

.table-shops th {
  color: #343434;
}

.table-shops td {
  color: #6e6e6e;
  padding-top: 20px;
  padding-bottom: 20px;
}

.table-shops__row {
  display: flex;
  justify-content: space-between;
}

.table-shops__row:nth-child(odd):not(.table-shops__row--title) {
  border-bottom: 1px solid #b1b1b1;
}

.table-shops .table-shops__row--title > th:nth-child(1) {
  width: 56px;
}

.table-shops .table-shops__row--title > th:nth-child(2) {
  width: 147px;
}

.table-shops .table-shops__row--title > th:nth-child(3) {
  width: 189px;
}

.table-shops .table-shops__row--title > th:nth-child(4) {
  width: 315px;
}

.table-shops::-webkit-scrollbar-track {
  background-color: #7c7c7c;
  border-radius: 5px;
}

.table-shops::-webkit-scrollbar {
  width: 5px;
  height: 5px;
  background-color: #e42626;
}

.table-shops::-webkit-scrollbar-thumb {
  border-radius: 5px;
  background-color: #e42626;
}

.popup {
  display: none;
  width: 616px;
  background-color: #ffffff;
  border-radius: 5px;
  position: absolute;
  top: 25%;
  left: 25%;
  box-shadow: 0 30px 38px rgba(37, 37, 37, 0.1);
  animation: showPopup 0.5s ease 0s 1 normal forwards;
}

.popup__wrapper {
  padding: 30px 19px 35px 30px;
}

.popup.popup--show {
  display: block;
}

@keyframes showPopup {
  0% {
    transform: scale(0);
  }

  100% {
    transform: scale(1);
  }
}

@keyframes closePopup {
  0% {
    opacity: 1;
    transform: scale(1);
  }

  100% {
    opacity: 0;
    transform: scale(0.6);
  }
}

.form__input--shake-error {
  animation: shake 0.4s;
}

@keyframes shake {
  0%, 100% {
    transform: translateX(0);
  }

  10%, 30%, 50%, 70%, 90% {
    transform: translateX(-15px);
  }

  20%, 40%, 60%, 80% {
    transform: translateX(15px);
  }
}

.form__title {
  font-weight: 500;
  font-size: 20px;
  color: #343434;
  margin-bottom: 42px;
}

.form__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.form__label {
  position: relative;
}

.form__select {
  margin-top: 7px;
  border: 1px solid #cdcdcd;
  border-radius: 4px;
  padding: 7px 15px 7px 14px;
  width: 240px;
  font-weight: 400;
  font-size: 12px;
  line-height: 21px;
  color: #7c7c7c;
  cursor: pointer;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}

.form__label:not(.form__label--domain)::after {
  position: absolute;
  content: "";
  top: 44px;
  left: 215px;
  width: 20px;
  height: 20px;
  background-image: url("../img/content/icons/icon-select-down.svg");
  background-repeat: no-repeat;
  z-index: 1000;
  pointer-events: none;
  transition: opacity 0.3s ease-out;
}

.form__list-item {
  display: flex;
  flex-direction: column;
  width: 240px;
}

.form__list-item--domain {
  position: relative;
  margin-bottom: 27px;
  width: 100%;
}

.form__list-item:not(.form--domain) {
  margin-bottom: 24px;
}

.form__input {
  position: relative;
  margin-top: 8px;
  padding: 8px 160px 8px 14px;
  background-color: #e6e6e9;
  font-weight: 500;
  font-size: 14px;
  color: #7d7d7d;
  border: none;
  box-sizing: border-box;
}

.form__list-item--domain::before {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  content: ".work5.ru";
  top: 29px;
  right: 0;
  width: 151px;
  height: 37px;
  background-color: #6666CC;
  color: #ffffff;
  font-weight: 500;
  z-index: 100;
}

.form__description {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 44px;
}

.form__notice-text {
  font-size: 12px;
  line-height: 18px;
  width: 488px;
  color: #7d7d7d;
}

.form__notice-text a {
  color: #6666CC;
}

.form__notice-svg {
  margin-right: 16px;
}

.form__btn-wrapper {
  display: flex;
  flex-wrap: wrap;
}

.form__btn-close {
  position: absolute;
  top: 30px;
  right: 20px;
  border: none;
  background-color: transparent;
  padding: 0;
  transition: transform 0.3s ease-out;
}

.form__btn-close-svg {
  stroke: #656565;
  transition: stroke 0.3s ease-out;
}

.form__btn-close:hover,
.form__btn-close:focus {
  transform: scale(1.2);
  transition: transform 0.3s ease-out;
}

.form__btn-close:hover .form__btn-close-svg,
.form__btn-close:focus .form__btn-close-svg {
  stroke: #e42626;
  transition: stroke 0.3s ease-out;
}

.form__btn-close:active {
  opacity: 0.5;
}

.form__btn-close-svg:active .form__btn-close-svg {
  opacity: 0.5;
}

.form__input--error {
  outline: 2px dashed #e42626;
}

@media (max-width: 1199px) {
  .button {
    font-size: 12px;
    padding: 5px 18px 5px 12px;
  }

  .account__wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .account__content {
    font-size: 12px;
    padding: 20px 20px 20px 20px;
  }

  .account__content-breadcrumb {
    margin-bottom: 20px;
    width: 100%;
    transform: translateX(-11px);
  }

  .account__nav {
    display: flex;
    flex-direction: column;
    align-items: center;
    min-height: initial;
    max-width: 100%;
  }

  .account__sign-up-container {
    display: flex;
    flex-direction: column;
    padding-left: 24px;
  }

  .account__nav-item {
    display: flex;
  }

  .account__nav-link {
    font-size: 30px;
  }

  .sign-up {
    margin-right: 10px;
    margin-bottom: 20px;
  }

  .breadcrumb-item {
    font-size: 16px;
    width: max-content;
  }
}

@media (max-width: 830px) {
  .account__nav {
    padding: 20px 20px;
  }

  .account__nav-link {
    font-size: 20px;
  }

  .sign-up {
    margin-right: 0;
    margin-bottom: 30px;
  }

  .breadcrumb-item {
    font-size: 14px;
  }

  .table-shops {
    width: 100vw;
    overflow-x: auto;
    padding: 0;
  }

  .popup {
    left: 2%;
    width: 100vw;
  }

  .popup__wrapper {
    padding: 15px 15px;
  }
}

@media (max-width: 604px) {
  .form__input {
    padding-right: 90px;
  }

  .form__list-item--domain::before {
    width: 80px;
  }

  .form__notice-text {
    margin-top: 15px;
  }
}

@media (max-width: 370px) {
  .button--form {
    margin-bottom: 15px;
    margin-right: 0;
  }

  .account__nav-link {
    font-size: 16px;
  }

  .form__input {
    padding-right: 15px;
  }

  .form__list-item--domain::before {
    display: none;
  }
}

@media (max-width: 340px) {
  .sign-up {
    flex-direction: column;
    width: initial;
  }

  .sign-up__item--avatar {
    width: 80vw;
    min-width: 206px;
  }

  .sign-up__item--mail {
    margin-right: 0;
    margin-bottom: 20px;
  }
}

@media (max-width: 300px) {
  .form__select {
    width: 80vw;
  }
}

@media (max-width: 280px) {
  .form__label:not(.form__label--domain)::after {
    opacity: 0;
    transition: opacity 0.3s ease-out;
  }
}

@media (max-width: 230px) {
  .account__nav-item--current::before {
    left: -10px;
    transition: left 0.2s ease-out;
  }
}